import React from "react";
import { Layout, Popover, Button, Menu, Avatar } from "antd";
import { connect } from "react-redux";
import createHistory from "../../util/create_history";
import { logoutuser } from "../../action/resource";
import { Link } from "react-router-dom";

const { Header } = Layout;
// const { SubMenu } = Menu;

const MyHeader = props => {
  const content = (
    <Menu mode="inline">
      <Menu.Item key="5">
        {" "}
        <Link to="/projects">Project Page</Link>
      </Menu.Item>
      <Menu.Item key="6">
        <Link to="/info">Personal info</Link>
      </Menu.Item>
    </Menu>
  );
  return (
    <Header className="flex">
      <div
        style={{
          height: "100%",
          display: "flex",
          justifyContent: "space-between"
        }}
      >
        <div style={{ color: "white", alignSelf: "center" }}>
          <Link to="/">
            <span
              style={{
                padding: "8px 16px",
                borderRadius: "12px",
                backgroundColor: "#333",
                boxShadow: "0 0 3px 3px rgba(200,200,200,0.1)",
                border: "1px solid rgba(200,200,200,0.14)"
              }}
            >
              KAYER
            </span>
          </Link>
        </div>
        <div
          style={{
            color: "white",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center"
          }}
        >
          {props.auth.isAuth ? (
            <Popover placement="bottomRight" content={content} trigger="click">
              <div
                style={{
                  height: "100%",
                  marginRight: "10px",
                  width: 60,
                  backgroundColor: "rgba(140,140,140,.6)"
                }}
              >
                <Avatar
                  src={props.auth.user.avatar}
                  shape="circle"
                  size={32}
                  style={{ margin: "0 14px" }}
                  icon="user"
                />
              </div>
            </Popover>
          ) : (
              <></>
            )}
          {props.auth.isAuth ? (
            <Button
              onClick={() => {
                createHistory.push("/signin");
                props.logoutuser();
              }}
              style={{
                marginLeft: "8px"
              }}
              type="danger"
              size={"small"}
            >
              LogOut
            </Button>
          ) : (
              <>
                <Link to="/signin">
                  <Button type="link"> Sign in</Button>{" "}
                </Link>
                <Link to="/signup">
                  {" "}
                  <Button type="link"> Sign up</Button>{" "}
                </Link>
              </>
            )}
        </div>
      </div>
    </Header>
  );
};

const mapStateToProps = state => ({
  auth: state.auth
});

export default connect(
  mapStateToProps,
  { logoutuser }
)(MyHeader);
