import {
  SET_USER,
  OUT_USER,
  CLEAR_ERR,
  UPDATE_PROFILE
} from "../action/resource";
import { setTokenToHeader } from "../axios_config";
// import createHistory from "../util/create_history";

const initialState = {
  isAuth: false,
  user: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case OUT_USER:
      localStorage.removeItem("jwtToken");
      setTokenToHeader(false);
      return {
        isAuth: false,
        user: {}
      };
    case SET_USER:
      return {
        isAuth: true,
        user: action.payload
      };

    case UPDATE_PROFILE:
      return {
        isAuth: state.isAuth,
        user: {
          ...state.user,
          ...action.payload
        }
      };

    default:
      return state;
  }
}

export const clearerr = () => dispatch => {
  dispatch({
    type: CLEAR_ERR,
    payload: ""
  });
};
