import React from "react";
import { Layout, Menu, Affix } from "antd";
import { connect } from "react-redux";
import { getProjects } from "../../action/project";
import { Link } from "react-router-dom";
const { Sider } = Layout;

const AsideMenuInProjects = props => {
  if (props.auth.isAuth) {
    return (
      <Affix offsetTop={0}>
        <Sider breakpoint="md" collapsedWidth="0">
          <Menu style={{ height: "100vh" }} theme="dark" mode="inline">
            <Menu.Item key={`0`}>
              {" "}
              <Link to={`/projects`}>{"All Projects"}</Link>
            </Menu.Item>
            <Menu.Item key={`1`}>
              {" "}
              <Link to={`/projects/${props.match.params.projectId}`}>
                {"Projects Page"}
              </Link>
            </Menu.Item>
            <Menu.Item key={`2`}>
              {" "}
              <Link to={`/projects/${props.match.params.projectId}/backlog`}>
                {"Project Backlog"}
              </Link>
            </Menu.Item>
            <Menu.Item key={`3`}>
              {" "}
              <Link to={`/projects/${props.match.params.projectId}/timeline`}>
                {"Project Timeline"}
              </Link>
            </Menu.Item>
            <Menu.Item key={`4`}>
              {" "}
              <Link to={`/projects/${props.match.params.projectId}/pool`}>
                {"Project Pool"}
              </Link>
            </Menu.Item>
            <Menu.Item key={`5`}>
              {" "}
              <Link to={`/projects/${props.match.params.projectId}/storymap`}>
                {"Project Story Map"}
              </Link>
            </Menu.Item>
          </Menu>
        </Sider>
      </Affix>
    );
  } else {
    return <></>;
  }
};

const mapStateToProps = state => ({
  auth: state.auth,
  projects: state.projects
});

export default connect(
  mapStateToProps,
  { getProjects }
)(AsideMenuInProjects);
