import {
	GET_PROJECTS_MEMBER,
	ADD_PROJECT_MEMBER,
	GET_PROJECTS_MEMBER_CLEAR_LOADING,
	GET_PROJECTS_MEMBER_LOADING,
} from "../action/resource";

export default (state = { loading: false, members: [] }, action) => {
	switch (action.type) {
		case GET_PROJECTS_MEMBER:
			return { ...state, ...action.payload };
		case GET_PROJECTS_MEMBER_LOADING:
			return { ...state, loading: true };
		case GET_PROJECTS_MEMBER_CLEAR_LOADING:
			return { ...state, loading: false };
		case ADD_PROJECT_MEMBER:
			return {
				...state,
				members: state.members.concat({ user: { ...action.payload } }),
			};
		default:
			return state;
	}
};
