export const getProjects = () => (dispatch, getStete) => {
	dispatch({ type: GET_WAIT });

	axios
		.get("/user/getUserProjects")
		.then((res) => {
			dispatch({ type: GET_PROJECTS, payload: res.data });
			dispatch({ type: CLEAR_WAIT });
		})
		.catch(function(error) {
			if (error.response) {
				if (error.response.status !== 401) {
					const err = error.response.data;
					dispatch({ type: GET_ERR, payload: err });
				}
			} else if (error.request) {
				dispatch({
					type: GET_ERR,
					payload: { err: ["chech your net connection"] },
				});
			} else {
				dispatch({
					type: GET_ERR,
					payload: { err: ["unknown err"] },
				});
			}
			dispatch({ type: CLEAR_WAIT });
		});
};

