export const getProjectActivitySprint = (projectId) => (dispatch, getStete) => {
	dispatch({ type: GET_ACT_SPR_LOADING });
	const getActivity = () => {
		return axios.post("/activity/getprojectactivities", { projectId });
	};

	const getSprint = () => {
		return axios.post("/sprint/getprojectsprints", { projectId });
	};

	const getMilstone = () => {
		return axios.post("/milestone/getmilestones", { projectId });
	};

	axios
		.all([getActivity(), getSprint(), getMilstone()])
		.then(
			axios.spread((actRes, sprRes, milRes) => {
				dispatch({
					type: GET_ACTIVITY,
					payload: actRes.data.activities,
				});
				dispatch({ type: GET_SPRINT, payload: sprRes.data.sprints });
				dispatch({
					type: GET_MILESTONE,
					payload: milRes.data.milestones,
				});
			}),
		)
		.catch(function(error) {
			if (error.response) {
				if (error.response.status !== 401) {
					const err = error.response.data;
					dispatch({ type: GET_ERR, payload: err });
				}
			}
			if (error.request) {
				dispatch({
					type: GET_ERR,
					payload: { err: ["chech your net connection"] },
				});
			} else {
				dispatch({
					type: GET_ERR,
					payload: { err: ["unknown err"] },
				});
			}
		});
	dispatch({ type: CLEAR_ACT_SPR_LOADING });
};

