export const moveItemInAllPool = (
	projectId,
	itemId,
	sName,
	sId,
	sIndex,
	dName,
	dId,
	dIndex,
) => (dispatch) => {
	dispatch({ type: LOADING_ALL_SPRINTS_AND_POOL });

	if (sName === dName && sId === dId) {
		dispatch({
			type: MOVE_ITEM_SPRINTS,
			payload: { sName, sId, sIndex, dName, dId, dIndex },
		});
	} else {
		dispatch({
			type: MOVE_ITEM_SPRINTS,
			payload: { sName, sId, sIndex, dName, dId, dIndex },
		});
		axios
			.post("/story/changesprint", {
				projectId,
				storyId: itemId,
				sprintId: dId,
			})
			.then((res) => {
				console.log("send", {
					projectId,
					storyId: itemId,
					sprintId: dId,
				});

				// dispatch(getProjectSprintPool(projectId));
			})
			.catch(function(error) {
				if (error.response) {
					if (error.response.status !== 401) {
						dispatch(getProjectSprintPool(projectId));
					}
				}
				if (error.request) {
					dispatch({
						type: GET_ERR,
						payload: { err: ["an err occured . dnd does apply"] },
					});
				} else {
					dispatch({
						type: GET_ERR,
						payload: {
							err: [
								"we have problem with your connection .maybe from us !! .maybe from you. now you can drag and drop (doesn't apply ) and each time  sea an me. ",
							],
						},
					});
				}
			});
	}

	dispatch({ type: CLEAR_LOADING_ALL_SPRINTS_AND_POOL });
};

