import { GET_PROJECTS_DETAIL, UPDATE_PROJECT } from "../action/resource";

export default (state = {}, action) => {
  switch (action.type) {
    case GET_PROJECTS_DETAIL:
      return action.payload;
    case UPDATE_PROJECT:
      return {
        ...state,
        projectInfo: {
          ...state.projectInfo,
          ...action.payload
        }
      };
    default:
      return state;
  }
};
