import React, { lazy, Suspense } from "react";
import { Router, Route, Switch } from "react-router-dom";
import createHistory from "./util/create_history";
import "./axios_config";
import { Layout, Spin } from "antd";

import {
  FORGETPASS,
  SIGNIN,
  SIGNUP,
  VALIDATE_EMAIL,
  CHANGE_PASS
} from "./util/link";
import jwtToken from "jwt-decode";
import { setTokenToHeader } from "./axios_config";
import store from "./store";
import { SET_USER, OUT_USER } from "./action/resource";

import PrivateRoute from "./component/util/PrivateRoute";
import PublicRoute from "./component/util/PublicRoute";

// layout component
import MyFooter from "./component/layout/Footer";
import MyHeader from "./component/layout/Header";
import AsideMenu from "./component/layout/AsideMenu";
import AsideMenuInProjects from "./component/layout/AsideMenuInProjects";
// end layout

// info
const Info = lazy(() => import("./component/pages/Info"));
//

//landing page
const LandingPage = lazy(() => import("./component/pages/LandingPage"));
//end

// auth componennt
const Signup = lazy(() => import("./component/pages/Signup"));
const Signin = lazy(() => import("./component/pages/Signin"));
const ValidEmail = lazy(() => import("./component/pages/ValidEmail"));
const ChangePassword = lazy(() => import("./component/pages/ChangePassword"));
const ForgetPassword = lazy(() => import("./component/pages/ForgetPassword"));
const ProjectStoryId = lazy(() =>
  import("./component/pages/ProjectStoryDetail")
);
// end auth componennt

// project routes
const Projects = lazy(() => import("./component/pages/ProjectCreate"));
const ProjectDetails = lazy(() => import("./component/pages/PeojectDetails"));
const ProjectBackLog = lazy(() => import("./component/pages/ProjectBackLog"));
const ProjectPool = lazy(() => import("./component/pages/ProjectPool"));
const ProjectStoryMap = lazy(() => import("./component/pages/ProjectStoryMap"));
const ProjectTimeline = lazy(() => import("./component/pages/ProjectTimeline"));
// end project

const { Content } = Layout;

if (localStorage.jwtToken) {
  const secret = localStorage.jwtToken;
  const user = jwtToken(secret);
  setTokenToHeader(secret);
  store.dispatch({
    type: SET_USER,
    payload: user
  });
  const exp = Date.now() / 1000;
  if (exp > user.exp) {
    createHistory.push("/signin");
    store.dispatch({ type: OUT_USER });
  }
}

function App() {
  return (
    <Router history={createHistory}>
      <Layout>
        <MyHeader />
        <Layout>
          <Switch>
            <Route path="/projects" exact component={AsideMenu} />
            <Route
              path="/projects/:projectId"
              component={AsideMenuInProjects}
            />
          </Switch>

          <Content
            style={{
              margin: "24px 16px 0",
              minHeight: "80vh"
            }}
          >
            <Suspense
              fallback={
                <>
                  <Spin />
                  <Spin />
                </>
              }
            >
              <Switch>
                <PrivateRoute path="/projects" exact component={Projects} />
                <PrivateRoute
                  path="/projects/:projectId"
                  exact
                  component={ProjectDetails}
                />
                <PrivateRoute
                  path="/projects/:projectId/backlog"
                  exact
                  component={ProjectBackLog}
                />

                <PrivateRoute
                  path="/projects/:projectId/pool"
                  exact
                  component={ProjectPool}
                />
                <PrivateRoute
                  path="/projects/:projectId/storymap"
                  exact
                  component={ProjectStoryMap}
                />
                <PrivateRoute
                  path="/projects/:projectId/timeline"
                  exact
                  component={ProjectTimeline}
                />
                <PrivateRoute
                  path="/projects/:projectId/:storyId"
                  exact
                  component={ProjectStoryId}
                />

                <PrivateRoute exact path="/info" component={Info} />

                <PublicRoute
                  restricted={true}
                  exact
                  path={SIGNUP}
                  component={Signup}
                />
                <PublicRoute
                  restricted={true}
                  exact
                  path="/"
                  component={LandingPage}
                />
                <PublicRoute
                  restricted={true}
                  exact
                  path={SIGNIN}
                  component={Signin}
                />
                <PublicRoute
                  restricted={true}
                  exact
                  path={VALIDATE_EMAIL}
                  component={ValidEmail}
                />
                <PublicRoute
                  restricted={true}
                  exact
                  path={CHANGE_PASS}
                  component={ChangePassword}
                />
                <PublicRoute
                  restricted={true}
                  exact
                  path={FORGETPASS}
                  component={ForgetPassword}
                />
              </Switch>
            </Suspense>
          </Content>
        </Layout>
        <MyFooter />
      </Layout>
    </Router>
  );
}

export default App;
