import {
	GET_TIMELINE,
	LOADING_TIMELINE,
	CLEAR_LOADING_TIMELINE,
} from "../action/resource";

export default (
	state = { loading: false, sprints: [], milestone: [] },
	action,
) => {
	switch (action.type) {
		case GET_TIMELINE:
			return {
				...state,
				sprints: action.payload.sprints,
				milestone: action.payload.milestone,
			};
		case LOADING_TIMELINE:
			return { ...state, loading: true };
		case CLEAR_LOADING_TIMELINE:
			return { ...state, loading: false };

		default:
			return state;
	}
};
