import {
  GET_PROJECTS_STORY,
  GET_PROJECTS_STORY_LOADING,
  GET_PROJECTS_STORY_CLEAR_LOADING,
  ADD_PROJECT_STORY,
  SELECT_PROJECT_STORY,
  DESELECT_PROJECT_STORY,
  UPDATE_PROJECT_STORY
} from "../action/resource";

export default (
  state = { loading: false, stories: [], selectedStory: {} },
  action
) => {
  switch (action.type) {
    case GET_PROJECTS_STORY:
      return { ...state, stories: action.payload };
    case GET_PROJECTS_STORY_LOADING:
      return { ...state, loading: true };
    case GET_PROJECTS_STORY_CLEAR_LOADING:
      return { ...state, loading: false };
    case SELECT_PROJECT_STORY:
      return { ...state, selectedStory: action.payload };
    case UPDATE_PROJECT_STORY:
      return {
        ...state,
        stories: [
          ...state.stories.filter(
            story => story.storyId !== action.payload.storyId
          ),
          action.payload
        ]
      };
    case DESELECT_PROJECT_STORY:
      return { ...state, selectedStory: {} };

    case ADD_PROJECT_STORY:
      return {
        ...state,
        stories: state.stories.concat({ ...action.payload })
      };
    default:
      return state;
  }
};
