import {
	GET_ACT_SPR_LOADING,
	CLEAR_ACT_SPR_LOADING,
	GET_ACTIVITY,
	GET_SPRINT,
	ADD_ACTIVITY,
	ADD_SPRINT,
	GET_MILESTONE,
	ADD_MILESTONE,
} from "../action/resource";

export default (
	state = { loading: false, sprints: [], activities: [], milestones: [] },
	action,
) => {
	switch (action.type) {
		case GET_ACTIVITY:
			return { ...state, activities: action.payload };
		case GET_MILESTONE:
			return { ...state, milestones: action.payload };
		case GET_SPRINT:
			return { ...state, sprints: action.payload };
		case GET_ACT_SPR_LOADING:
			return { ...state, loading: true };
		case CLEAR_ACT_SPR_LOADING:
			return { ...state, loading: false };
		case ADD_ACTIVITY:
			return {
				...state,
				activities: state.activities.concat(action.payload),
			};
		case ADD_SPRINT:
			return {
				...state,
				sprints: state.sprints.concat(action.payload),
			};
		case ADD_MILESTONE:
			return {
				...state,
				milestones: state.milestones.concat(action.payload),
			};
		default:
			return state;
	}
};
