import {
	GET_STORY_MAP,
	LOADING_STORY_MAP,
	CLEAR_LOADING_STORY_MAP,
} from "../action/resource";

export default (
	state = { loading: false, activiteis: [], sprints: [] },
	action,
) => {
	switch (action.type) {
		case GET_STORY_MAP:
			return {
				...state,
				sprints: action.payload.sprints,
				activiteis: action.payload.activities,
			};
		case LOADING_STORY_MAP:
			return { ...state, loading: true };
		case CLEAR_LOADING_STORY_MAP:
			return { ...state, loading: false };

		default:
			return state;
	}
};
