import axios from "axios";
import store from "./store";
import { OUT_USER, GET_ERR } from "./action/resource";
import createHistory from "./util/create_history";
// axios.defaults.baseURL = "http://172.16.5.65:8585/api/";
axios.defaults.baseURL = "http://78.38.77.51:8585/api/";
axios.interceptors.response.use(
  function(response) {
    console.log("sounds good");
    return response;
  },
  error => {
    const UNAUTHORIZED = 401;
    const { status } = error.response;
    if (status === UNAUTHORIZED) {
      store.dispatch({
        type: GET_ERR,
        payload: {
          err: "PLEASE LOG IN FIRST"
        }
      });
      createHistory.push("/signin");
      store.dispatch({
        type: OUT_USER
      });
    }
    return Promise.reject(error);
  }
);

export const setTokenToHeader = token => {
  if (token) {
    axios.defaults.headers.common["Authorization"] = token;
  } else {
    delete axios.defaults.headers.common["Authorization"];
  }
};
