import {
	CLEAR_LOADING_ALL_SPRINTS_AND_POOL,
	LOADING_ALL_SPRINTS_AND_POOL,
	GET_ALL_SPRINTS_AND_POOL,
	MOVE_ITEM_SPRINTS,
} from "../action/resource";

export default (state = { Pool: {}, Others: [], loading: false }, action) => {
	switch (action.type) {
		case GET_ALL_SPRINTS_AND_POOL:
			return {
				...state,
				Pool: action.payload.Pool,
				Others: action.payload.Others || [],
			};

		case MOVE_ITEM_SPRINTS:
			const { sName, sId, sIndex, dName, dId, dIndex } = action.payload;
			let removed;
			let findIndex;
			if (sName === "pool") {
				[removed] = state.Pool.stories.splice(sIndex, 1);
			} else {
				findIndex = state.Others.findIndex(
					(item) => item.sprintId === +sId,
				);
				[removed] = state.Others[findIndex].stories.splice(sIndex, 1);
			}

			if (dName === "pool") {
				state.Pool.stories.splice(dIndex, 0, removed);
			} else {
				findIndex = state.Others.findIndex(
					(item) => item.sprintId === +dId,
				);
				[removed] = state.Others[findIndex].stories.splice(
					dIndex,
					0,
					removed,
				);
			}

			return state;

		case LOADING_ALL_SPRINTS_AND_POOL:
			return {
				...state,
				loading: true,
			};
		case CLEAR_LOADING_ALL_SPRINTS_AND_POOL:
			return {
				...state,
				loading: false,
			};
		default:
			return state;
	}
};
